
// headline
#js-cover {
	margin-bottom: 0px;
	.slick-dots {
		top: auto;
		bottom: 50px;
		width: 100%;
		/* left: 40%; */
		margin: 0 auto;
	}
	.slick-dots li {
		margin: 0px 5px;
		width: 35px;
		height: 35px;
	}
	.slick-dots li button {
		width: 35px;
		height: 35px;
		background: $cl_grey;
		border-radius: 20px;
		padding: 2px
	}
	.slick-dots li.slick-active button {
		width: 35px;
		height: 35px;
		background: $cl_primary;
		border-radius: 50%;
		padding: 2px
	}
	.slick-dots li button:before {
		content: '';
	}
	.slick-arrow{
		padding: 0;
		background: transparent;
		border: none;
		color: $cl_white;
		position: absolute;
		z-index: 1;
		top: 0;
		height: 100%;
		cursor: pointer;
		-webkit-transition: all 1s ease-out;
		transition: all 1s ease-out;
		box-shadow: none;
		&:hover, &:focus, &:active{
			outline: none;
			box-shadow: none;
        }
        .icon-right, .icon-left {
			padding: 15px;
			font-size: 1.5em;
			border-radius: 50%;
			background: $cl_secondary;
        }
        .icon-angle-left{
        	padding: 0px 10px 0 5px;
        }
    }
    .slick-prev{
    	left: -3rem;
    }
    .slick-next{  
    	right: -3rem;
	}
	.slick-slide{
		img{
			max-width: 100%;
			height: auto;		
			display: initial;
		}
	}
}
#js-testimonial {
	margin-bottom: 0px;
	.slick-arrow{
		padding: 0;
		background: transparent;
		border: none;
		color: $cl_white;
		position: absolute;
		z-index: 1;
		bottom: -40px;
		height: auto;
		cursor: pointer;
		-webkit-transition: all 1s ease-out;
		transition: all 1s ease-out;
		&:hover, &:focus, &:active{
			box-shadow: none;
			outline: none;
            box-shadow: none;
        }
        .icon-right, .icon-left {
			padding: 10px;
			font-size: 1em;
			background: $cl_primary;
        }
        .icon-angle-left{
        	padding: 0px 10px 0 5px;
        }
    }
    .slick-prev{
		left: 0rem;
    }
    .slick-next{  
		left: 3rem;
	}
	.slick-slide{
		img{
			max-width: 100%;
			height: auto;		
			display: initial;
		}
	}
}

.slider-for{
	.slick-arrow{
		padding: 0;
		background: transparent;
		border: none;
		color: $cl_white;
		position: absolute;
		z-index: 1;
		top: 0;
		height: 400px;
		cursor: pointer;
		-webkit-transition: all 1s ease-out;
		transition: all 1s ease-out;
		&:hover, &:focus, &:active{
			box-shadow: none;
			outline: none;
            // opacity: 1;
        }
        .icon-right, .icon-left {
        	padding: 10px;
			font-size: 1em;
			border-radius: 50%;
			border: 1px solid $cl_white;
        }
        .icon-angle-left{
        	padding: 0px 10px 0 5px;
        }
    }
    .slick-prev{
		left: 0rem;
		@include min-responsive($mntablet){
			left: -5rem;
        }
    }
    .slick-next{  
		right: 0rem;
		@include min-responsive($mntablet){
			right: -5rem;
        }
    }
}