.social {
    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        @include min-responsive($mntablet) {
            justify-content: flex-start;
        }
    }

    &__item {
        margin: 0 20px;
        font-size: 18px;
        @include min-responsive($mntablet) {
            margin: 10px 0;
            margin-right: 25px;
            color: $cl_grey;
        }
    }

    &__icon {}
}