.footer {
    position: relative;
    padding: 50px 0;
    background: url('../images/bg-footer.jpg');
    background-size: cover;

    @include max-responsive($mdtablet) {
        background-position: bottom;
        background-size: 1245px;
        background-repeat: no-repeat;
    }

    &__wrapper {
        color: $cl_black;
        padding-top: 135px;
    }

    &__intro {
        padding-right: 50px;
        &__text{
            line-height: 1.5;
        }
    }

    &__menu {
        padding-left: 95px;
        color: white;

        &__item {
            margin: 10px 17px;

            @include min-responsive($mntablet) {
                margin: 10px 0;
            }
        }
    }

    &__logo {
        margin: 25px;

        @include min-responsive($mntablet) {
            margin: 0;
        }
    }

    &__icon {
        display: block;
        margin: 10px auto 20px;

        img {
            max-width: 100%;
        }
    }

    &__copyright {
        text-align: center;
        color: white;
        padding-left: 95px;
    }

    &__social {
        @include min-responsive($mntablet) {
            padding-right: 70px;
            padding-top: 10px;
        }
    }
}