.checkbox {
    label {
        padding: 15px 0 !important;
        margin: 2px 50px !important;
        @include max-responsive($mntablet){
            margin: 2px 0px 2px 40px !important;
        }
    }
    input[type="checkbox"]+label {
        &::before {
            content: "";
            width: 20px;
            height: 20px;
            position: absolute;
            top: 19px;
            left: -30px;
            border: 1px solid #747474;
            background: #e5e5e5;

        }
    }
    input[type="checkbox"]:checked+label {
        &::before {
            content: "X";
            font-size: 16px;
            padding-left: 4px;
            font-family: $font_body;
            font-weight: 400;
            color: $cl_primary;
            letter-spacing: 5px;
        }

        
    }
}