// KCM base scss
// Versions 1.0
// Created by VISUALDEV

body {
	font-family: $font_body;
	color: $cl_black;
	-webkit-font-smoothing: antialiased;
	-webkit-text-shadow: rgba(0, 0, 0, .01) 0 0 1px;
	text-shadow: rgba(0, 0, 0, .01) 0 0 1px;
	margin: 0;
	padding: 0;
	overflow-x: hidden;

}

html {
	margin: 0;
	padding: 0;
}

html,
body {
	width: 100%;
	height: 100%;
}

*,
*:before,
*:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}


a {
	color: inherit;
	text-decoration: none;
	//display: inline-block;
	vertical-align: top;
	outline: 0;
	@include base_transition('all');

	&:hover {
		// color: #ccc;
		// color: $bg;
		text-decoration: none;
	}
}

a:focus,
button:focus {
	outline: 0;
	outline: -webkit-focus-ring-color auto 0;
	outline-offset: 0;
}

strong {
	font-weight: 700;
}

img {
	max-width: 100%;
	width: auto;
	height: auto;
	vertical-align: top;
	border: 0;
	-ms-interpolation-mode: bicubic;
}

input:focus::-webkit-input-placeholder,
input:focus:-moz-placeholder,
input:focus::-moz-placeholder,
input:focus:-ms-input-placeholder {
	color: transparent;
}

h1,
h2,
h3 {
	margin: 0;
}

.clearfix {
	*zoom: 1;

	&:after,
	&:before {
		display: table;
		content: "";
		line-height: 0;
	}

	&:after {
		clear: both;
	}

	position: relative;
}

.mt1 {
	margin-top: 10px;
}

.mt2 {
	margin-top: 20px;
}

.mt3 {
	margin-top: 30px;
}

.mt4 {
	margin-top: 40px;
}

.mt6 {
	margin-top: 60px;
}

.mt10 {
	margin-top: 100px !important;
}

.mb1 {
	margin-bottom: 10px;
}

.mb2 {
	margin-bottom: 20px;
}

.mb3 {
	margin-bottom: 30px;
}

.mb4 {
	margin-bottom: 40px;
}

.pad0 {
	padding: 0;
}

.pv2 {
	padding: 20px 0px;
}

.fr {
	float: right;
}

.display-table {
	display: table;
	@include display_full();
	position: relative;
	z-index: 1;
}

.display--block {
	display: block;
}

.display-center {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
	@include display_full();
}

.text-left {
	text-align: left !important;
}

.text-right {
	text-align: right;
}

.text-center {
	text-align: center;
}

.border {
	&-bottom {
		border-bottom: 1px solid $cl_grey;
	}
}

ol,
ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.left {
	float: left;
}

.right {
	float: right;
}

.table {
	display: table;
	margin: 0 auto;
}


.pink::selection {
	background: $cl_orange;
	color: $cl_white;
}

.pink::-moz-selection {
	background: $cl_orange;
	color: $cl_white;
}

.display-center {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
	width: 100%;
	height: 100%;
}

.display-table {
	display: table;
	width: 100%;
	height: 100%;
	position: relative;
	z-index: 1;
}

.overflow {
	overflow-x: hidden;
}

.ps3 {
	padding: 0 3.5rem !important;
}

.display-desktop {
	display: none !important;

	@include min-responsive($mntablet) {
		display: block !important;
	}
}

.display-mobile {
	display: block !important;

	@include min-responsive($mntablet) {
		display: none !important;
	}
}

#section-3 {
	.header__img {
		background: url(../images/pijaru-text.png) no-repeat !important;
		background-size: contain !important;
	}
}