 /* ------------------------------------------
   FIXED HEADER
 --------------------------------------------- */

 .header {
     background: transparent;
     position: fixed;
     z-index: 10;
     width: 100%;
     left: 0;
     top: -2px;
     margin: 0;
     transition: all .2s ease-in-out;
     overflow: hidden;

     @include min-responsive($mntablet) {
         padding: 15px 0 95px;
     }

     @include min-responsive($xltablet) {
         padding: 15px 0 150px;
     }

     &__circle {
         position: absolute;
         top: -195%;
         left: -130px;
         width: 665px;
         height: 500px;
         border-radius: 50%;
         background: $cl_primary;
         z-index: -1;
         transition: all .2s ease-in-out;

         @include min-responsive(1920px) {
             top: -135%;
             left: -90px;
             width: 1300px;
             height: 500px;
         }

         @include max-responsive(1920px) {
             top: -135%;
             left: -90px;
             width: 965px;
             height: 500px;
         }

         @include max-responsive($lgtablet) {
             top: -195%;
             left: -130px;
             width: 665px;
             height: 500px;
         }

         @include max-responsive($mntablet) {
             top: -230%;
             left: -130px;
             width: 425px;
         }
     }

     &__wrapper {
         display: flex;
         justify-content: space-between;
         align-items: center;
     }

     &__logo {
         position: relative;
         padding: 0;
         float: left;
         transition: all .2s ease-in-out;
         width: 200px;

         @include max-responsive($mdtablet) {
             margin-left: 95px;
         }

         @include max-responsive($mntablet) {
             margin-left: 0px;
             width: 180px;
         }
     }

     &__img {
         position: relative;
     }

     &__nav {
         display: flex;
         justify-content: space-between;
         align-items: center;
     }

     &__list {
         display: flex;
         margin-right: 20px;
     }

     &__link {
         margin: 0 10px;

         a {
             color: $cl_secondary;
             font-weight: 700;
         }
     }

     &.sticky {
         background: white;
         box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.1);
         padding: 15px 0;

         .header {
             &__logo {
                 width: 100px;
             }

             &__circle {
                 top: -445%;
                 width: 400px;
                 left: -45px;

                 @include min-responsive($xltablet) {
                     left: -90px;
                     width: 750px;
                 }

                 @include max-responsive($mntablet) {
                     left: -145px;
                 }
             }
         }
     }
 }