.jelly {
    position: absolute;

    &--1 {
        top: 60px;
        right: -230px;
    }
    &--2 {
        top: 65px;
        left: -200px;
    }
    &--3{
        top: -400px;
        right: -300px;
    }
    &--4{
        top: -365px;
        left: -400px;
    }
    &--about {
        top: 60px;
        right: -100px;
    }
    &--insurance {
        top: 60px;
        right: -100px;
    }
}

.star {
    position: absolute;
    &--1{
        top: 0;
        right: 420px;
    }
    &--2{
        top: 170px;
        left: 70px;
    }
    &--3{
        top: -75px;
        right: 150px;
        @include max-responsive($mdtablet){
            right: 30px;
        }
    }
    &--4{
        top: -75px;
        left: 150px;
        @include max-responsive($mdtablet){
            left: 30px;
        }
    }
    &--about{
        top: 100px;
        right: 605px;
    }
    &--insurance{
        top: 100px;
        right: 140px;
    }
}