.testimonial{
    &__img {
        min-height: 200px;
        margin-bottom: 20px;
        img{
            border-radius: 50%;
        }
    }

    &__name {
        text-align: left;
        font-family: $font_body;
        font-weight: 300;
        font-size: 16px;
        letter-spacing: 2px;
        text-transform: uppercase;
    }

    &__text {
        text-align: left;
        font-family: $font_title;
        font-size: 22px;
        color: black;
        margin-bottom: 20px;
        height: 92px;
        overflow: hidden;
    }
    &__loc {
        text-align: left;
        font-family: $font_body;
        font-weight: 300;
        font-size: 16px;
    }
}