.about{
    margin-top: 220px;
    margin-bottom: 100px;
    @include min-responsive($xltablet){
        margin-top: 250px;
    }
    &Wp{
        margin: 100px 0;
        &__wrapper{
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &__img{
            flex: 0 0 60%;
            padding-right: 10px;
        }
        &__box{
            flex: 0 0 40%;
        }
    }
    &__content{
        font-family: $font_body;
        font-size: 20px;
        line-height: 32px;
    }
    &__img{
        padding-right: 20px;
    }
    &__box{
        padding-left: 15px;
    }
}