.card {
	position: relative;
	margin: 0;

	&__item {
		padding: 30px;
		border-radius: 10px;
		border: 1px solid black;
		box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
		margin: 0 0 40px;
		position: relative;
		background: white;
		@include min-responsive($xltablet) {
			margin: 0;
			padding: 30px;
		}
	}

	&__action {
		position: absolute;
		z-index: 1;
		top: 0px;
		left: 0px;
	}

	&__remove {
		width: 30px;
		height: 30px;
		display: block;
		border-radius: 100%;
		padding: 7px;
		transition: .2s ease-in-out;
		&:hover {
			background: $cl_grey;
			box-shadow: 0 2px 2px rgba(0, 0, 0, .1);
		}
	}
	&__desc{
		font-family: $font_body;
        color: $cl_grey;
        font-size: 16px;
        line-height: 32px;
        padding: 0 10px;
	}
	&__price{
		font-family: $font_body;
		color: black;
		font-weight: 700;
        font-size: 24px;
        line-height: 32px;
		padding: 0 10px;
		margin: 10px 0;
	}
}