.filter {

    &__body {
        background: white;
        display: inline-block;
        border-radius: 10px;
        border: 1px solid black;
        padding: 15px;
    }

    &__list {
        padding-bottom: 10px;
        list-style: none;
        margin: 0;
        padding: 0;
        margin-bottom: 10px;
        margin: auto;
        transition: background .25s linear;
        -webkit-transition: background .25s linear;
        max-height: 235px;
        overflow: hidden;
        .mCSB_dragger{
            &:hover{
                .mCSB_dragger_bar {
                    background-color: $cl_grey;
        
                    &:hover, &:active, &:focus, &:visited {
                        background-color: $cl_grey;
        
                    }
                }
            }
        }
        .mCSB_dragger .mCSB_dragger_bar {
            background-color: $cl_grey;
            width: 6px;
            &:hover, &:active, &:focus, &:visited {
                background-color: $cl_grey;

            }
        }
        .tooltip {
            position: absolute;
            z-index: 1;
            display: block;
            padding: 5px;
            font-size: 11px;
            visibility: visible;
            margin-top: -2px;
            bottom:170%;
            margin-left: -4em;
            position: relative;
            width: 75px;
        }
        
        .tooltip .tooltip-arrow {
            bottom: 0;
            left: 50%;
            margin-left: -5px;
            position: absolute;
            width: 0;
            height: 0;
        }
        
        .tooltip-inner {
            max-width: 500px;
            padding: 3px 8px;
            color: darker($cl_grey, 50%);
            text-align: center;
            text-decoration: none;
            -webkit-border-radius: 4px;
            -moz-border-radius: 4px;
            border-radius: 4px;
        }
        
        .ui-slider {
            background: $cl_grey repeat-x bottom left;
            border-bottom: 1px solid #EBEAE5;
            height: 2px;
            margin: 0;
            padding: 0px;
            margin: 80px 20px 30px;
            cursor: pointer;
            &:focus{
                outline: none;
            }
        }
        
        .ui-slider-handle {
            background: $cl_primary;
            border-bottom: 1px solid #a8a79f;
            border-right: 1px solid #a8a79f;
            height: 20px;
            width: 10px;
            margin: 0;
            padding: 0px;
            display: inline-block;
            cursor: pointer;
            &:focus{
                outline: none;
            }
        }
        
        .ui-slider-range {
            height: 10px;
            border-bottom: 3px solid red;
            position: relative;
        }
        
        .ui-slider-handle {
            background: $cl_primary !important;
            border-bottom: 1px solid #a8a79f !important;
            border-right: 1px solid #a8a79f !important;
            height: 20px !important;
            width: 20px !important;
            margin: 0 !important;
            padding: 0px !important;
            display: inline-block !important;
            top: -10px !important;
            border-radius: 50%;
        }
        

        li {
            display: block;
            position: relative;
            color: #aaa;
            width: 100%;
            height: auto;
            margin-bottom: 25px;
            @include max-responsive($mntablet){
                margin-bottom: 40px;
            }
            label {
                font-family: $font_body;
                display: block;
                position: relative;
                font-weight: 500;
                font-size: 15px;
                padding: 10px 10px 10px 70px;
                margin: 5px auto;
                height: 30px;
                z-index: 9;
                cursor: pointer;
                -webkit-transition: all .25s linear;
                color: black;
            }
        }

        input {
            display: none;
        }
    }

    &__slider {
        width: 100%;
    }
}

