.button {
    font-family: $font_body;
    font-weight: 700;
    padding: 5px 35px;
    
    &--secondary {
        background: white;
        border: 1px solid $cl_secondary;
        color: $cl_secondary;
        border-radius: 5px;
        transition: .2s ease-in-out;
        cursor: pointer;
        
        &:hover,&:focus,&:visited,&:active {
            background: $cl_secondary;
            color: white;
        }
    }
    &--primary{
        background: $cl_primary;
        border: 1px solid $cl_primary;
        color: white;
        border-radius: 25px;
        transition: .2s ease-in-out;
        cursor: pointer;

        &:hover,&:focus,&:visited,&:active {
            background: white;
            color: $cl_primary;
        }
    }
}