.intro {
    margin: 100px 0;

    &__video {
        margin: 50px 0;

        iframe {
            width: 100%;
            max-height: 600px;

            @include min-responsive($xltablet) {
                height: 61vh;
            }

            @include max-responsive($xltablet) {
                height: 70vh;
            }

            @media screen and (device-height: 700px) {
                height: 70vh;
            }
        }
    }
}