.cover{
    padding: 0 75px;
    &__bg{
        position: relative;
        z-index: -1;
    }
    &__wrapper{
        position: relative;
        height: 100vh;
        display: flex;
        align-items: center;
        @include max-responsive($xltablet){
            height: 82vh;
        }
    }
    &__item{
        padding: 0 25px;
        overflow: hidden;
    }
    &__box{
        width: 45%;
    }
    &Text{
        margin: 10px 0;
        &--big{
            font-family: $font_title;
            font-size: 65px;
            line-height: 80px;
            color: $cl_primary;
            @include max-responsive($mntablet){
                font-size: 40px;
                line-height: 44px;
            }
        }
        &--small{
            font-family: $font_body;
            font-size: 20px;
            font-weight: 300;
            color: black;
            margin: 20px 0;
        }
    }
    &__img{
        position: absolute;
        right: -25px;
        bottom: 0;
        z-index: -1;
        max-width: 700px;
        @include max-responsive($xltablet){
            max-width: 560px;
        }
        @include max-responsive($mdtablet){
            right: -165px;
        }
    }
}