.seputar{
    padding-right: 10px;
    &__img {
        min-height: 200px;
        margin-bottom: 20px;
    }

    &__title {
        text-align: left;
        font-family: $font_title;
        font-size: 36px;
        margin: 10px 0;
    }

    &__text {
        text-align: left;
        font-family: $font_body;
        font-size: 16px;
        color: $cl_grey;
        margin-bottom: 20px;
        height: 110px;
        overflow: hidden;
        font-weight: 300;
    }

    &__link {
        display: block;
        padding: 20px 0;
        font-weight: 700;
        text-align: left;
        border-top: 1px solid $cl_grey;
        transition: .2s ease-in-out;
        &:hover{
            color: $cl_primary;
        }
        span{
            font-size: 13px;
            font-weight: bolder;
        }
    }
}