.product {
    &Wp {
        margin: 200px auto 100px;

        &__wrapper {
            position: relative;
        }

        &__star {
            position: absolute;
            top: -50px;
            right: -125px;
            z-index: -1;
            @include max-responsive($mdtablet){
                right: 0;
            }
            @include max-responsive($mntablet){
                display: none;
            }
        }

        &__list {
            display: flex;
            margin: 100px 0 20px;
        }
        &__box{
            border-right: 1px solid $cl_grey;
            padding: 0 15px;
        }
        &__item {
            text-align: center;

            &:last-child {
                .productWp__box{
                    border-right: none;
                }
            }
        }

        &__img {
            min-height: 200px;
            margin-bottom: 20px;
        }

        &__title {
            text-align: left;
            font-family: $font_title;
            font-size: 22px;
            height: 52px;
            overflow: hidden;
        }

        &__text {
            text-align: left;
            font-family: $font_body;
            font-size: 18px;
            color: $cl_grey;
            margin-bottom: 20px;
            height: 118px;
            overflow: hidden;
        }

        &__link {
            display: block;
            padding: 20px 0;
            font-weight: 700;
            text-align: left;
            border-top: 1px solid $cl_grey;
            transition: .2s ease-in-out;
            margin: 0 15px;
            &:hover {
                color: $cl_primary;
            }
        }

    }
}