.title {
    margin: 10px 0;

    &--big {
        font-family: $font_title;
        color: $cl_primary;
        font-size: 39px;
        line-height: 80px;
        @include max-responsive($mntablet){
            font-size: 30px;
        }
    }

    &--small {
        font-family: $font_title;
        color: $cl_primary;
        font-size: 24px;
        line-height: 32px;
        padding: 0 10px;
        &est{
            font-family: $font_title;
            font-size: 14px;
        }
    }
    &--dark{
        color: black;
    }
    &__text{
        padding: 10px 0;
    }
}