.belt{
    position: relative;
    padding-top: 100px;
    padding-bottom: 100px;
    &::before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 90%;
        border-top: 1px solid $cl_grey;
        z-index: -2;
    }
    &__item{
        &:first-child{
            padding-right: 30px;
        }
        &:last-child{
            padding-left: 30px;
        }
    }
}