.partner{
    position: relative;
    padding-top: 100px;
    padding-bottom: 100px;
    &::before{
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 90%;
        border-top: 1px solid $cl_grey;
        z-index: -2;
    }
    &__list{

    }
    &__item{
        margin: 20px 0;
        padding: 0 20px;
    }
    &__img{
        width: 185px;
        height: 185px;
        margin: 0 auto;
        img{
            max-width: 100%;
            max-height: 100%;
        }
    }
}